export async function getHRDashboardData() {
  const totalEmployees = MOCK_ACCOUNTS.filter(a => a.role === 'Employee').length;
  const pendingLeaveRequests = MOCK_LEAVE_REQUESTS.filter(lr => lr.status === 'Pending').length;

  const stats = [
      { title: 'Total Employees', value: String(totalEmployees), change: '' },
      { title: 'On Time Percentage', value: `0%`, change: '' },
      { title: 'Active Job Postings', value: '4', change: 'External API' },
      { title: 'Pending Leave Requests', value: String(pendingLeaveRequests), change: '' },
  ];

  return {
    stats,
    recentActivities: []
  };
}

export async function getPastAnnouncements() {
    return MOCK_ANNOUNCEMENTS.map(a => {
        const postedBy = MOCK_ACCOUNTS.find(acc => acc.id === a.posted_by_id);
        return {
            ...a,
            postedBy: postedBy ? `${''}${postedBy.first_name} ${postedBy.last_name}` : 'Unknown',
            date: format(new Date(a.posted_at), 'yyyy-MM-dd')
        };
    }).sort((a, b) => new Date(b.posted_at) - new Date(a.posted_at));
}

export async function createAnnouncement(title, content, status) {
    const hrUserId = 'HR-001';
    const newAnnouncement = {
        id: MOCK_ANNOUNCEMENTS.length + 1,
        title,
        content,
        posted_by_id: hrUserId,
        status,
        posted_at: new Date().toISOString()
    };
    MOCK_ANNOUNCEMENTS.push(newAnnouncement);
}

